<template>
     <h1> Pokemon Page</h1>
    <div v-if="pokemon">
        <img src="pokemon.sprites.front_default" :alt="pokemon.name"/>
        <h2>{{ pokemon.name }}</h2>
      
       

    </div>
    <p>{{ id }}</p>
  
</template>
<script>
export default {
    data() {
        return {
            pokemon: null, 
        };
    },
    props: {
        id :{
            required : true,
            type: Number,
        },
        item: {
      type: Object,
      default: () => {}
    },
    },
    methods: {
        async getPokemon (){
            try{
                const resp = await fetch(
                'https://pokeapi.co/api/v2/pokemon/${this.id}'
                ).then((response) => response.json());
                console.log(resp);
                this.pokemon=resp;
                

            }catch (error){
                this.$router.push("/");
                console-console.log("informacion no disponible");
            }

        },
    },
    whatch :{
        id(){
            this.getPokemon();
        },
    },
    created () {
        this.getPokemon();
    },
    };

</script>
